import React, { Component } from 'react'
import '../main.css'
import  requit  from '../api'
import Cookie from 'js-cookie'
import pubsub from 'pubsub-js'

class A extends React.Component {
    render() {
        return (
            <div>
                <fieldset className="form-group">
                    <input
                        className="form-control form-control-lg"
                        type="text"
                        placeholder="Your Name"
                        onBlur={this.onBlur3}
                    />
                </fieldset>
            </div>
        );
    }
}
export default class Login extends Component {
    constructor(props) {
        super(props);
        this.onBlur1 = this.onBlur1.bind(this);
        this.onBlur2 = this.onBlur2.bind(this);
        this.onBlur3 = this.onBlur3.bind(this);
        this.submit = this.submit.bind(this);
        this.state={
            email:'',
            password:'',
            userName:''
          }
    }
    onBlur1(e){
        this.setState({
            email:e.target.value
        })
        // console.log(e.target.value)
    }
    onBlur2(e){
        // console.log(e.target.value)
        this.setState({
            password:e.target.value
        })
    }
    onBlur3(e){
        // console.log(e.target.value)
        this.setState({
            userName:e.target.value
        })
    }
    submit(e){
        e.preventDefault();
        let user = {
            email:this.state.email,
            password:this.state.password,
            username:this.state.userName
        }
        // console.log(user)
         requit({
            method: 'post', 
            url:'/api/users/login',
            data:{
                user:user

            }
        }).then(res=>{
            // console.log(res);
          Cookie.set('auth',res.data.user.token)
        //   console.log(res.data.user.token);
        this.props.history.push('/')
          pubsub.publish('auth',res.data.user.token)
        })
    }


    isLogin() {
        return this.props.match.path === "/login";
    }

    render() {
        return (
            <div className="auth-page">
                <div className="container page">
                    <div className="row">

                        <div className="col-md-6 offset-md-3 col-xs-12">
                            <h1 className="text-xs-center">{this.isLogin() ? "Sign in" : "Sign up"}</h1>
                            <p className="text-xs-center">
                                <a href="">{this.isLogin() ? "Need an account" : " Have an account"}</a>
                            </p>

                            <ul className="error-messages">
                                <li>That email is already taken</li>
                            </ul>

                            <form>
                                {this.isLogin() ? '' : <A />}

                                <fieldset className="form-group">
                                    <input 
                                    className="form-control form-control-lg"
                                     type="text" placeholder="Email"
                                     onBlur={this.onBlur1} />
                                </fieldset>
                                <fieldset className="form-group">
                                    <input className="form-control form-control-lg" 
                                    type="password" placeholder="Password" 
                                    onBlur={this.onBlur2}/>
                                </fieldset>
                                <button onClick={this.submit} className="btn btn-lg btn-primary pull-xs-right">
                                    {this.isLogin() ? " Sign in" : "  Sign up"}
                                </button>
                            </form>
                        </div>

                    </div>
                </div>
            </div>
        )
    }
}
