import requit  from '../api'
import React, { Component } from 'react'
import { NavLink } from "react-router-dom";
import '../main.css'
import './index.css'
import { addFavorite, deleFavorite } from '../api/favorite';


export default class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: [],
      tag: [],
      rows: 30,
      currentPage: 0, //当前页码
      groupCount: 5, //页码分组，显示7个页码，其余用省略号显示
      startPage: 1,  //分组开始页码
      totalPage: 1 ,//总页数
      favoritedisabled : false
    }

    this.onclick = this.onclick.bind(this)
    this.fnFavorite = this.fnFavorite.bind(this)
  }

 async fnFavorite(data){
  await this.setState({
    favoritedisabled : true
  })
    // console.log(data);
    if(data.favorited){

     await deleFavorite(data).then((res)=>{
        console.log('取消',res);
      })
      data.favorited = false
      data.favoritesCount += -1

      let key = this.state.data.indexOf(data);
      console.log('取消-----',key);
      this.state.data[key] = data;
      await this.setState({  //异步
        data : this.state.data
      })
      await this.setState({
        favoritedisabled : true
      })
    
     
    }else{
      await addFavorite(data).then((res)=>{
        console.log('添加',res);
      })
      data.favorited = true
      data.favoritesCount += 1
      
      let key = this.state.data.indexOf(data);
      console.log('添加-----',key);
      this.state.data[key] = data;
      await this.setState({  //异步
        data : this.state.data
      })

    }
    await this.setState({
      favoritedisabled : false
    })

  }

  componentDidMount() {
    // requit({
    //   method: 'get',
    //   url: '/api/articles'
    // }).then(res => {
    //   console.log(res);
    //   // console.log(res.data.articles)

    //   this.setState({
    //     data: res.data.articles
    //   })

    // })
    requit({
      method: 'get',
      url: '/api/tags'
    }).then(res => {
      // console.log(res);
      // console.log(res.data.articles)

      this.setState({
        tag: res.data.tags
      })

    })
    requit({
      method: 'get',
      url: '/api/articles',
      params: {
        limit: this.state.rows,
        offset:this.state.currentPage
      }
    }).then(res => {
      if (res.status === 200) {
        this.setState({
          data: res.data.articles,
          totalPage: Math.ceil(res.data.articlesCount / this.state.rows),
        })

      }
    })
  }
  onclick = (i) =>{
    this.setState({
      currentPage : i
    })
    console.log(i);
    requit({
      method: 'get',
      url: '/api/articles',
      params: {
        limit: this.state.rows,
        offset:i * this.state.rows
      }
    }).then(res => {
      if (res.status === 200) {
        this.setState({
          data: res.data.articles,
        })
        console.log(this.state.data);
      }
    })
  }
  render() {
    // const  totalPage = this.state.totalPage
    return (
      <div className="home-page">

        <div className="banner">
          <div className="container">
            <h1 className="logo-font">conduit</h1>
            <p>A place to share your knowledge.</p>
          </div>
        </div>

        <div className="container page">
          <div className="row">

            <div className="col-md-9">
              <div className="feed-toggle">
                <ul className="nav nav-pills outline-active">
                  <li className="nav-item">
                    <a className="nav-link disabled" href="">Your Feed</a>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link active" href="">Global Feed</a>
                  </li>
                </ul>
              </div>

              {this.state.data.map((item, index) => {
                // console.log(item)
                return (
                  <div className="article-preview" key={index}>
                    <div className="article-meta">
                      <a href="profile.html"><img src={item.author.image} /></a>
                      <div className="info">
                        <a href="" className="author">{item.author.username}</a>
                        <span className="date">{item.createdAt}</span>
                      </div>
                      <button className="btn btn-outline-primary btn-sm pull-xs-right" 
                      disabled={this.state.favoritedisabled}
                      onClick={()=>{
                          this.fnFavorite(item)
                        }
                      }>
                        <i className="ion-heart"></i> {item.favoritesCount}
                      </button>
                    </div>
                    <a href="" className="preview-link">
                      <h1>{item.title}</h1>
                      <p>{item.description}</p>
                      <span>read more.....</span>
                    </a>
                  </div>
                )
              })}


            </div>

            <div className="col-md-3">
              <div className="sidebar">
                <p>Popular Tags</p>

                <div className="tag-list">
                  {this.state.tag.map((item, index) => {
                    // console.log(item);
                    return (
                      <a href="" className="tag-pill tag-default" key={index}>{item}</a>
                    )
                  })}
                </div>
              </div>
            </div>


            <div>
              <ul className='pagination'>
                {
                  Array.from(Array(this.state.totalPage),(e, i) => {
      
                    return (
                      <li className='page-item' key={i}>
                        <NavLink to="/" className={this.state.currentPage === i ? "navlink " : 'page-link'} onClick={() => {
                          this.onclick(i)
                        }}>{i+1}</NavLink>
                      </li>
                    )
                  })
                }


              </ul>
            </div>
          </div>
        </div>

      </div>
    )
  }
}
