import React, { Component } from 'react'
import '../main.css'
import { NavLink } from "react-router-dom";
import Cookie from 'js-cookie'
import pubsub from 'pubsub-js'



export default class Header extends Component {
    constructor(props){
        super(props)
        this.state={
            auth:''
        }
    }
    componentDidMount(){
        var that = this
        // console.log(this,'1');
        pubsub.subscribe('auth',function(msg,data){
            // console.log(this,'2');
            that.setState({
                auth:data
            })  
        })
    }

    render(){
  
        if(Cookie.get("auth")){
         
            return (
              <div>
                <nav className="navbar navbar-light">
                  <div className="container">
                    <a className="navbar-brand" href="index.html">
                      conduit
                    </a>
                    <ul className="nav navbar-nav pull-xs-right">
                      <li className="nav-item">
                        <NavLink className="nav-link active" to="/">
                          Home
                        </NavLink>
                      </li>
                      <li className="nav-item">
                        <NavLink className="nav-link" to="/editor" activeClassName="active">
                          <i className="ion-compose"></i>&nbsp;New Article
                        </NavLink>
                      </li>
                      <li className="nav-item">
                        <NavLink className="nav-link" to="/Set" activeClassName="active">
                          <i className="ion-gear-a"></i>&nbsp;Settings
                        </NavLink>
                      </li>
                      <li className="nav-item">
                        <NavLink className="nav-link ng-binding active" ui-sref-active="active" ui-sref="app.profile.main({ username: $ctrl.currentUser.username })"  to="/profile" activeClassName="active">
                          <img className="user-pic" ng-src="https://z3.ax1x.com/2021/04/29/gFXnCF.png" src="https://z3.ax1x.com/2021/04/29/gFXnCF.png"/>
                        jeason-来
                        </NavLink>
                      </li>
                    </ul>
                  </div>
                </nav>
              </div>
            );
          }else{
            return (
              <div>
                <nav className="navbar navbar-light">
                  <div className="container">
                    <a className="navbar-brand" href="index.html">
                      conduit
                    </a>
                    <ul className="nav navbar-nav pull-xs-right">
                      <li className="nav-item">
                        <NavLink className="nav-link active" to="/">
                          Home
                        </NavLink>
                      </li>
                      <li className="nav-item">
                        <NavLink className="nav-link" to="/login" activeClassName="active">
                          Sign in
                        </NavLink>
                      </li>
                      <li className="nav-item">
                        <NavLink className="nav-link" to="/register" activeClassName="active">
                          Sign up
                        </NavLink>
                      </li>
                    </ul>
                  </div>
                </nav>
              </div>
            );
          }
        
    }
}
